
.NewNote {
  background-color: #4182d6;
}
.NewNote .General__Container {
  padding-top: 1.458vh;
  padding-bottom: 1.458vh;
}
.NewNote__Box {
  background-color: #fff;
  height: 9.375vh;
  min-height: 45px;
}
.NewNote__Field {
  border: none;
  height: 9.375vh;
  min-height: 45px;
  max-height: 9.375vh;
  resize: none;
  padding: 2%;
  font-size: 14px;
  width: calc(100% - (2.6rem + 4%));
}
.NewNote__Btn {
  background-color: transparent;
  background-image: url("../assets/svg/send.svg");
  background-size: 2rem;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
  float: right;
  height: 9.375vh;
  min-height: 45px;
  max-height: 9.375vh;
  padding: 0 2%;
  text-indent: -9999px;
  width: calc(2.3rem + 4%);
}


