
.SearchBar {
  background-color: #4182d6;
}
.SearchBar .General__Container {
  padding-top: 1.458vh;
  padding-bottom: 1.458vh;
}
.SearchBar__Box {
  background-color: #fff;
  height: 6.459vh;
  min-height: 35px;
}
.SearchBar__Field {
  border: none;
  height: 6.459vh;
  min-height: 35px;
  width: calc(100% - (2rem + 4%));
  padding-left: 2%;
}
.SearchBar__Btn {
  background-color: transparent;
  background-image: url("../assets/svg/search.svg");
  background-size: 1.166rem;
  background-repeat: no-repeat;
  background-position: center;
  float: right;
  border: none;
  cursor: pointer;
  height: 6.459vh;
  min-height: 35px;
  padding: 0 2%;
  text-indent: -9999px;
  width: calc(1.6rem + 4%);
}


