
.Annotation {
  background-color: #fff;
  border: 1px solid #4182d6;
  overflow: hidden;
  margin-bottom: 10px;
}
.Annotation__Text,
.Annotation__Text--big {
  color: #333;
  float: left;
  font-size: 1.3rem;
  height: 12vh;
  min-height: 60px;
  overflow: auto;
  line-height: 2.2rem;
  padding: 12px 15px 14px;
  width: calc(100% - 36px);
}
.Annotation__Text--big,
.Annotation__Text--big--big {
  font-size: 1.7rem;
  line-height: 1.9rem;
}
.Annotation__Options {
  background: #4182d6;
  float: right;
  height: 12vh;
  min-height: 60px;
  padding: 5px 0;
  width: 36px;
}
.Annotation__OptionsBtn,
.Annotation__OptionsBtn--favorite,
.Annotation__OptionsBtn--edit,
.Annotation__OptionsBtn--favorite--edit {
  background-color: transparent;
  background-size: 1.2rem;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
  height: calc((12vh - 10px) / 2);
  min-height: 25px;
  width: 36px;
}
.Annotation__OptionsBtn--favorite,
.Annotation__OptionsBtn--favorite--favorite,
.Annotation__OptionsBtn--edit--favorite,
.Annotation__OptionsBtn--favorite--edit--favorite {
  background-image: url("../assets/svg/star.svg");
}
.Annotation__OptionsBtn--edit,
.Annotation__OptionsBtn--favorite--edit,
.Annotation__OptionsBtn--edit--edit,
.Annotation__OptionsBtn--favorite--edit--edit {
  background-image: url("../assets/svg/edit.svg");
}


