  height: 7.291vh;
  min-height: 7.291vh;
}
.NotificationBar__Text {
  background-color: transparent;
  background-image: url("../assets/svg/plus.svg");
  background-size: 1.166rem;
  background-repeat: no-repeat;
  background-position: left calc(3.645vh - 0.56rem);
  color: #fff;
  display: block;
  height: 7.291vh;
  line-height: 7.291vh;
  text-decoration: none;
  padding-left: 1.666rem;
  width: 100%;
}


